import React from 'react';
import Square from './Square';

class Board extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            squares: Array(9).fill(null),
            oIsNext: true,
        }
    }

    handleClick(i) {
        const squares = this.state.squares.slice();
        if (calculateWinner(squares) || squares[i]){
            return;
        }
        squares[i] = this.state.oIsNext ? 'O' : 'X';
        this.setState({
            squares: squares,
            oIsNext: !this.state.oIsNext,
        })
    }

    renderSquare(i) { 
        return <Square 
        value={this.state.squares[i]}
        onClick={() => this.handleClick(i)} />;
    }

    render() {
        const winner = calculateWinner(this.state.squares);
        let status;
        if (winner) {
            status = "Winner: " + winner;
        } else {
            if (this.state.squares.includes(null)) {
                status = `Next Player: ${ this.state.oIsNext? '0': 'X'}`;
            } else {
                status = 'The game ended in a draw';
            }
        }
        return (
            <div>
                <div className="status">{status}</div>
                <div className="baord-row">
                    {this.renderSquare(0)}
                    {this.renderSquare(1)}
                    {this.renderSquare(2)}
                </div>
                <div className="baord-row">
                    {this.renderSquare(3)}
                    {this.renderSquare(4)}
                    {this.renderSquare(5)}
                </div>     
                <div className="baord-row">
                    {this.renderSquare(6)}
                    {this.renderSquare(7)}
                    {this.renderSquare(8)}
                </div>
                <div className="restart-game">
                    <button onClick={() => {this.setState({squares: Array(9).fill(null),oIsNext: !this.state.oIsNext})}}>Restart The Game</button>
                </div>                           
            </div>
        )
    }
}
 
function calculateWinner(squares){
    const combos = [
        [0, 1, 2],
        [3, 4, 5],
        [6, 7, 8],

        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8],

        [0, 4, 8],
        [2, 4, 6]
    ];
    for (let i = 0; i < combos.length; i++) {
        const [a, b, c] = combos[i];
        if (squares[a] && squares[a] === squares[b] && squares[b] === squares[c]){
            return squares[a];
        }
    }
    return null;
}

export default Board;